module.exports = "## Line\n\n线状图。\n\n## 安装\n\n### 通过 script\n\n```html\n<script src=\"https://unpkg.com/omi\"></script>\n<script src=\"https://unpkg.com/@omim/core@latest/chart/index.js\"></script>\n```\n\n### 通过 npm\n\n``` bash\nnpm install @omim/core\n```\n\n然后:\n\n```js\nimport '@omim/core/chart'\n```\n\n## 使用\n\n```html\n<m-line id=\"myLine\" width=\"600\" height=\"300\" \n  data=\"{\n    labels: ['Red', 'Blue', 'Yellow', 'Green', 'Purple', 'Orange'],\n    datasets: [{\n      label: '# of Votes',\n      data: [12, 19, 3, 5, 2, 3],\n      backgroundColor: 'rgba(255, 99, 132, .4)',\n      borderColor: 'rgb(255, 99, 132)',\n      borderWidth: 1\n    }]\n  }\"\n\n  options=\"{\n    responsive: true,\n    title: {\n      display: true,\n      text: 'Omi-chart Line Chart'\n    },\n    tooltips: {\n      mode: 'index',\n      intersect: false\n    },\n    hover: {\n      mode: 'nearest',\n      intersect: true\n    },\n    scales: {\n      xAxes: [{\n        display: true,\n        scaleLabel: {\n          display: true,\n          labelString: 'Month'\n        }\n      }],\n      yAxes: [{\n        display: true,\n        scaleLabel: {\n          display: true,\n          labelString: 'Value'\n        }\n      }]\n    }\n  }\">\n</m-line>\n```\n\n## 更新图表\n\n```js\n//获取该元素\nvar chart = document.querySelector('#myLine')\n//更改图表的数据\nchart.props.data.datasets.forEach(dataset => {\n  dataset.data.forEach((item, index) => {\n    dataset.data[index] = Math.random() * 100\n  })\n})\n//更新图表视图\nchart.update()\n```\n\n## API\n\n### Props\n\n```jsx\n{\n  data: object,\n  options: object,\n  width: number,\n  height: number,\n  horizontal?: string\n}\n```"

